import { Component } from '@angular/core';
import { PdfViewerComponent } from './../pdf-viewer/pdf-viewer.component';
// import { PdfViewerComponent } from 'pdf-viewer';

@Component({
  selector: 'pdf-viewer-app',
  template: `
    <div class="mdl-layout mdl-js-layout mdl-layout--fixed-header mdl-layout--no-drawer-button">
        <header class="mdl-layout__header">
            <div class="mdl-layout__header-row">
                <span class="mdl-layout-title">ng2-pdf-viewer</span>
            </div>
        </header>
        <main class="mdl-layout__content">
            <div class="page-content">
                <div>
                    <label>PDF src</label>
                    <input type="text" placeholder="PDF src" [(ngModel)]="pdfSrc">
                </div>
                <div>
                    <label>Page:</label>
                    <input type="number" placeholder="Page" [(ngModel)]="page">
                </div>

                <pdf-viewer [src]="pdfSrc" [initialPage]="page" [original-size]="true" style="display: block;"></pdf-viewer>

            </div>
        </main>
    </div>
  `,
  directives: [PdfViewerComponent]
})

export class AppComponent {
  pdfSrc: string = './pdf-test.pdf';
  page: number = 1;
}